    boxShadow:
      "0 2px 2px 0 " +
      fade(primaryColor, 0.14) +
      ", 0 3px 1px -2px " +
      fade(primaryColor, 0.2) +
      ", 0 1px 5px 0 " +
      fade(primaryColor, 0.12),
